@media (max-width: 480px) {
  /* Adjust home container size and layout for phones */
  .home-container {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin-top: 80px;
  }

  /* Reduce glitch effect opacity for mobile view */
  .home-container::before,
  .home-container::after {
    opacity: 0.3;
  }

  /* Center heading container and adjust font size for smaller screens */
  .heading-container {
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
 
  }

  .heading-text {
    font-size: 28px; /* Smaller font size for mobile */
    
  }

  .heading-logo {
    width: 30px;
    height: 30px;
  }

  /* Center content adjustments */
  .center-content {
    top: 40%;
    left: 50%;
    transform: translate(-50%, -40%);
  }

  .center-image {
    width: 150px;
    height: auto;
  }

  .center-text {
    font-size: 16px; /* Smaller text for mobile */
    padding: 0 20px; /* Add padding for better readability */
  }

  /* Adjust corner logos */
  .corner-logo {
    width: 30px;
    height: 30px;
  }

  /* Glitch form adjustments */
  .glitch-form {
    width: 90%; /* Make form responsive */
    height: auto; /* Allow height to adjust based on content */
    padding: 10px;
  }

  .form-heading {
    font-size: 24px; /* Smaller heading for mobile */
  }

  .form-body {
    gap: 10px;
  }

  .form-section {
    flex-direction: row; /* Stack form sections vertically */
    gap: 10px;
  }

  .form-input {
    font-size: 20px; /* Adjust input font size */
  }

  .form-submit {
    font-size: 20px; /* Adjust button font size */
    padding: 10px;
  }

  .form-button {
    font-size: 20px; /* Adjust button font size */
    width: 100px;
  }

  /* Adjust checkbox layout */
  .checkbox-plus {
    gap: 50px;
    flex-direction: column; /* Stack elements vertically on smaller screens */
  }

  .plus-sign {
    width: 30px;
    height: 30px;
    margin-left: 50px;
    margin-right: 50px;
    top: 30px;
   
  }
}
